import { Component, Element, Fragment, h, Host, JSX, Listen, Prop } from '@stencil/core';
import { ClickElementType, ClickElementTypes, EventAction, EventActions } from '../../utility/constants';
import { TDataLayer } from '@porsche/globaldatalayer-events/projects/pcom/all/types';
import { getHTMLElement, isString } from '../../utility/helper';
import { sendDataLayerEvent } from '../../services/analytics/data-layer-event-sender';

@Component({
  tag: 'phn-menu-item-card',
  styleUrl: 'menu-item-card.scss',
  scoped: true
})
export class MenuItemCard {
  @Prop() text!: string;
  @Prop() analyticsId!: string;
  @Prop() subtext = '';
  @Prop() elementId = '';
  @Prop() link = '';
  @Prop() datalayerEventAction: EventAction = EventActions.NAVIGATION_CLICK;
  @Prop() customDatalayerProperties: TDataLayer = {};
  @Prop() target = '_self';
  @Prop() iconLeft = '';

  @Element() el!: HTMLPhnMenuItemCardElement;

  @Listen('keydown', { passive: false })
  handleKeyDown(e: KeyboardEvent): void {
    if (e.key === 'Enter') {
      const anchorElement = getHTMLElement(e.target)?.querySelector('a');
      const targetElement = anchorElement ? anchorElement : getHTMLElement(e.target);

      if (targetElement) {
        targetElement.click();
        !anchorElement ? this.sendDataLayerEvent(ClickElementTypes.Button, this.elementId) : null;
      }
    }
  }

  private sendDataLayerEvent(clickElementType: ClickElementType, elementId: string): void {
    sendDataLayerEvent({
      analyticsId: this.analyticsId,
      text: this.text,
      elementId,
      clickElementType,
      link: this.link,
      customDatalayerProperties: this.customDatalayerProperties,
      datalayerEventAction: this.datalayerEventAction
    });
  }

  private renderTitle(): HTMLElement {
    return (
      <div class="phn-flex-row phn-align-center phn-justify-between title-container">
        <phn-p-heading tag="h3" size="medium">
          {this.text}
        </phn-p-heading>
        {isString(this.iconLeft) ? <phn-p-icon name={this.iconLeft}></phn-p-icon> : null}
      </div>
    );
  }

  private renderSubtitle(): JSX.Element | null {
    if (!isString(this.subtext)) {
      return null;
    }

    return (
      <phn-p-text
        class={'subtext'}
        color="contrast-medium"
        weight="regular"
        size="xx-small"
        tag="div"
        ellipsis="true"
        data-test-id="subtext"
      >
        {this.subtext}
      </phn-p-text>
    );
  }

  render(): JSX.Element {
    const body = (
      <Fragment>
        <div class="texts">
          {this.renderTitle()}
          {this.renderSubtitle()}
        </div>
        <slot />
      </Fragment>
    );

    if (isString(this.link))
      return (
        <a
          tabIndex={-1}
          onClick={() => {
            this.sendDataLayerEvent(ClickElementTypes.Link, this.elementId);
          }}
          class="card-link"
          href={this.link}
          target={this.target}
        >
          {body}
        </a>
      );

    return (
      <Host>
        <button
          tabIndex={-1}
          onClick={() => {
            this.sendDataLayerEvent(ClickElementTypes.Button, this.elementId);
          }}
        >
          {body}
        </button>
      </Host>
    );
  }
}
