@import '../../scss';

@mixin renderTitle() {
  .title-container {
    width: 100%;
    margin: 0;

    phn-p-heading {
      word-break: break-word;
      white-space: pre-line;
    }

    phn-p-icon{
      margin-left: p-px-to-rem(16px);
    }
  }
}

@mixin default-skeleton() {
  &.skeleton {
    pointer-events: none;
    user-select: none;
    background: $pds-theme-light-contrast-low;
    border-radius: 4px;

    & * {
      visibility: hidden;
    }
  }
}

:host {
  display: block;
  padding: p-px-to-rem(19px);
  margin-top: p-px-to-rem(16px);
  border-radius: 8px;
  background-color: $pds-theme-light-state-hover;
  transition: background-color 0.24s ease;
  cursor: pointer;

  button{
    all: unset;
    display: block;
    width: 100%;
  }

  @include default-skeleton();

  @include pds-media-query-min('s') {
    background-color: $pds-theme-light-background-base;
  }

  &:hover {
    @include pds-media-query-min('s') {
      background-color: $pds-theme-light-state-hover;
    }
  }

  a {
    text-decoration: none;
  }

  .subtext {
    color: $pds-theme-light-primary;
  }

  @include renderTitle();
}
